<!--  -->
<!-- Timeline -->
<!-- StoreVuex -->

<template>
  <!--  -->
  <div
    class="card"
    v-if="isShow"
    transition="expand"
  >
    <h4 class="card-header"></h4>
    <div class="card-inverse card-block" >
      <label class="control-label" for="textarea1"></label>
      <div class="text-xs-right">
      <textarea
        id="textarea1"
        v-model="message"
        placeholder=""
        class="form-control"
        rows="3"
        @input="saveStubMessage"
      ></textarea>
        <button @click="postMessage" class="btn btn-primary"></button>
      </div>
    </div>
  </div>
</template>

<script>
  import store from 'store'

  export default {
    props: {
      message: {
        type: String,
        default: ''
      }
    },
    data () {
      return {
        isShow: false // data  props 
      }
    },
    methods: {
      // 
      // post: function () {
      postMessage () {
        this.$dispatch('event-post', this.message)

        // this.isShow = false
        // let self = this
        // setTimeout(() => { self.isShow = true }, 1000)
      },
      saveStubMessage () {
        store.set('stubMessage', this.message)
      }
    },
    created () {
      let self = this
      setTimeout(() => { self.isShow = true }, 1000)

      this.message = store.get('stubMessage')
    }
  }
</script>

<style>
  /*  */
  .expand-transition {
    transition: all .3s ease;
    height: 300px;
    overflow: hidden;
  }

  /* .expand-enter  entering  */
  /* .expand-leave  leaving  */
  .expand-enter, .expand-leave {
    height: 0;
    opacity: 0;
  }
</style>



