import {
  SAVE_LOCALSTORAGE
} from '../mutation-types'

import store from 'store'

// initial state
// const state = store.get('vuexstorage')
// FIXME: 
const state = () => {
  return store.get('vuexstorage')
}

// mutations
const mutations = {
  [SAVE_LOCALSTORAGE] (state, newState) {
    store.set('vuexstorage', newState)
  }
}

export default {
  state,
  mutations
}



